import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { URL } from '../../Modelos/ConexionBackend';
import { Observable } from 'rxjs';
import { UsuarioAutenticadoOperacionesService } from "../../Servicios/UsuarioAutenticadoOperaciones/usuario-autenticado-operaciones.service";


@Injectable({
  providedIn: 'root'
})

// La siguiente clase perimite que un usuario salga de sesion

export class SalirSesionService {

  headers: HttpHeaders;
  token: string;

  /*
  En el siguiente metodo se inyecta HttpClient que permite conectarse con el backend y UsuarioAutenticadoOperacionesService
  , que permite tener acceso al usuario autenticado.
  */

  constructor(private http: HttpClient, private usuarioAutenticadoOperacionesServ: UsuarioAutenticadoOperacionesService) {
   
  }

  inicializarToken(){
    this.token = this.usuarioAutenticadoOperacionesServ.getUsuarioAutenticado().getToken();
  }

  inicializarHeaders(){
    this.headers = new HttpHeaders({Authorization: `Token ${this.token}`});
  }

  // El siguiente mtodo realiza el logout del sistema

  salirSesion(): Observable<any> {
    this.inicializarToken();
    this.inicializarHeaders();
    return this.http.post(`${URL}salirSesion`, {}, {headers: this.headers});   
  }

}
