// Modulos

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MostrarUsuariosModule } from "../app/usuario/mostrar-usuarios/mostrar-usuarios.module";

// Componentes

import { CrearEstudianteComponent } from '../app/estudiante/crear-estudiante/crear-estudiante.component';
import { MostrarEstudiantesComponent } from '../app/estudiante/mostrar-estudiantes/mostrar-estudiantes.component';
import { EditarEstudianteComponent } from './estudiante/editar-estudiante/editar-estudiante.component';
import { InsertarUsuarioComponent } from '../app/usuario/insertar-usuario/insertar-usuario.component';
import { MostrarUsuariosComponent } from './usuario/mostrar-usuarios/mostrar-usuarios.component';
import { EditarUsuarioComponent } from './usuario/editar-usuario/editar-usuario.component';
import { AutenticacionComponent } from './autenticacion/autenticacion.component';
import { PaginaPrincipalComponent } from './pagina-principal/pagina-principal.component';
import { GestionGeneralGuardGuard } from './Guards/GestionGeneralGuard/gestion-general-guard.guard';
import { GestionGuardGuard } from './Guards/GestionGuard/gestion-guard.guard';
import { VisualizadorGuardGuard } from './Guards/VisualizadorGuard/visualizador-guard.guard';


const routes: Routes = [
  {path: 'insertarEstudiante', component: CrearEstudianteComponent, canActivate: [ GestionGuardGuard ]},
  {path: 'mostrarEstudiantes', component: MostrarEstudiantesComponent, canActivate: [ VisualizadorGuardGuard ]},
  {path: 'editarStudent/:id', component: EditarEstudianteComponent, canActivate: [ GestionGuardGuard ]},
  {path: 'insertarUsuario', component: InsertarUsuarioComponent, canActivate: [ GestionGeneralGuardGuard ]},
  {path: 'mostrarUsuarios', component: MostrarUsuariosComponent, canActivate: [ GestionGuardGuard ]},
  {path: 'editarUsuario/:id', component: EditarUsuarioComponent, canActivate: [ GestionGeneralGuardGuard ]}, 
  {path: 'autenticacion', component: AutenticacionComponent}, 
  {path: 'paginaPrincipal', component: PaginaPrincipalComponent},
  {path: '**', redirectTo: 'paginaPrincipal'},

];


@NgModule({
  imports: [RouterModule.forRoot(routes, {anchorScrolling: 'enabled'})],
  exports: [RouterModule, MostrarUsuariosModule]
})
export class AppRoutingModule { }
