import { Injectable } from '@angular/core';
import { URL } from '../../Modelos/ConexionBackend';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UsuarioAutenticadoOperacionesService } from "../../Servicios/UsuarioAutenticadoOperaciones/usuario-autenticado-operaciones.service";

@Injectable({
  providedIn: 'root'
})

// El siguiente servicio permite obtener un estudiante desde el backend

export class ObtenerEstudianteService {

// Los siguientes atributos permiten hacer consultas al backend con el token que identifica al usuario autenticado

  headers: HttpHeaders;
  token: string;

/*
En el siguiente constructor se inyecta HttpClient que permitira acceder al backend, tambien se inyecta 
UsuarioAutenticadoOperacionesService, que permitira acceder al token del usuario autenticado
*/

  constructor(private http: HttpClient, private usuarioAutenticadoOperacionesServ: UsuarioAutenticadoOperacionesService) { }

  inicializarToken(){
    this.token = this.usuarioAutenticadoOperacionesServ.getUsuarioAutenticado().getToken();
  }

  inicializarHeaders(){
    this.headers = new HttpHeaders({Authorization: `Token ${this.token}`});
  }

// El siguiente metodo permite obtener un estudiante dado un id

  obtenerEstudiante(idEstudiante: string): Observable<any> {
    this.inicializarToken();
    this.inicializarHeaders();
    return this.http.get(`${URL}obtenerEstudiante/${idEstudiante}`, {headers: this.headers});
  }
}
